.About {
  background-image: url("../image/back.svg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  z-index: -10;
  height: 100%;
  /* border: 2px solid red; */
  display: grid;
  /* grid-template-columns: 40% 60%; 각 행(세로줄)의 길이 */
  grid-template-rows: 45% 55%; /*각 열(가로줄)의 길이*/
  gap: 10px; /* 자식요소간의 간격 */
  box-sizing: border-box;
  margin-top:2rem;
}
.box {
  /* background: yellow; */
  width: 100%;
  height: 100%;
  /* border: 1px solid #dee3eb; */
}
.box-flex {
  display: flex;
  flex-direction: row;
}
.box-flex > div {
  /* border: 1px solid red; */
}

/* profile 영역 */
.box-flex > div:first-child {
  width: 100%;
  flex: 4; /* 공간차지비율 */
  font-size: 1.2rem;
  position: relative;
  /* border: 4px solid yellow; */
}

.profile__photo {
  /* 이미지 비율 조정 */

  border: 2px solid  #ffffff;

  position: relative;
  background-image: url("../image/profile_rm.png");
  background-color:  #ffc3c0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  
  width: 35rem;
  height: 35rem;
  /* max-width: 220px; */
  transform: translate(18rem, 12rem);
  /* left: 5%; */
}
.profile__photo:after {
  /* 이미지 비율 조정 */
  position: absolute;
  width: inherit;
  height: inherit;
  display: block;
  content: "";
  border: 2px solid  #ffc3c0;
  transform: translate(-5%, -5%);
}

/* 나에대한 설명 */
.box-flex > div:last-child {
  width: 100%;
  flex: 6; /* 공간차지비율 */

  /* 폰트 */
  color:  #39393d;

  /* border: 6px solid olivedrab; */
  position: relative;
}
.text-box1 {
  /* border: 3px solid blue; */
  position: absolute;
  right: 15rem;
  top: 15%;
  text-align: right;
  transform: translateY(70px);
}
.text-box1 h2 {
  font-family: S-CoreDream-4Regular, sans-serif;
  color:#454547;

  margin-bottom: 2rem;
}
.text-box1 p {
  /* border: 2px solid black; */
  width: 80%;
  max-height: 150px;

  overflow: hidden;

  font-size: 1.4rem;
  float: right;
}



/* 우측 하단 이미지&글귀 */
.box2 {
  position: relative;
}
.box > img {
  /* border:4px solid blue; */
  position: absolute;
  bottom: 0;
}
.box > img:nth-child(1) {
  /* border: 4px solid skyblue; */
  position: absolute;
  width: 15rem;
  height: 15rem;
  right: 15rem;
  bottom: 25%;
}
.text-box2 {
  /* border: 4px solid black; */
  max-width: 30%;
  position: absolute;
  right: 15rem;
  bottom: 18%;
  text-align: right;
}




.text-box3 {
  font-size: 1.4rem;
  /* border: 4px solid gold; */
  position: absolute;
  display: flex;
  transform: translate(18rem, 10rem);
}
.text-box3 >div:first-child{
  font-weight: bold;
  margin-right: 4rem;
  min-width: 4rem;
}

@media (max-width: 1356px) {
  .text-box1 h2{
    font-size:2.2vw;
  }

}
@media (max-width: 1023px) {
  .text-box3{
    transform: translate(18rem, -5rem);
    font-size: 2rem;
  }
  .text-box1{
    right:5rem;
  }
  .box > img:nth-child(1) {
    right: 5rem;
    bottom: 30%;
  }
  .text-box2 {
    right: 5rem;
  }
}


@media (max-width: 767px) {
  .text-box1{
    right:5rem;
  }

  .box > img:nth-child(1) {
    right: 5rem;
    bottom: 30%;
  }
  .text-box2 {
    right: 5rem;
  }
}
@media (max-width: 639px) {
  .About{
    grid-template-rows:60% 40%;
  }
  .box-flex{
    display: block;
  }
  .box-flex > div:first-child{
    height: 30vh;
  }
  .profile__photo {
    /* 이미지 비율 조정 */
  
    width: 250px;
    height: 200px;
    /* max-width: 220px; */
    transform: translate(22rem, 6rem);
    /* left: 5%; */
  }
  .text-box2{display: none;}
  .box > img{
    display: none;
  }
  .text-box1{
    all: unset;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .text-box1 h2{
    font-size: 3.857vw;
  }
  .text-box1 p{
    font-size: 2.4rem;
  }
  .text-box3{
    transform: unset;
    position: relative;
    margin:0 auto;
    width:70rem;
    font-size:2.4rem;
  }
  .box > img:nth-child(1) {
    right: 5rem;
    bottom: 30%;
  }
  .text-box2 {
    right: 5rem;
  }
}