.MainPost {
  box-sizing: border-box;
  height: 100vh;
  overflow: hidden;
}

.container {
  position: relative;
  transform: translateY(10vh);
}

.greeting {
  color: #ffffff;
  text-shadow: 2px 0px rgb(57, 57, 61, 0.8), 0px 2px rgb(57, 57, 61, 0.8),
    -2px 0px rgb(57, 57, 61, 0.8), 0px -2px rgb(57, 57, 61, 0.8);
  word-spacing: 0.3rem;
  text-align: center;
  position: absolute;
  /* z-index: 10; */
  /* position:absolute일 때 세로 중앙정렬 */
  top: 50%;
  transform: translateY(50%);
  width: 70%;

  /* border:2px solid teal; */
}
h1 {
  font-family: S-CoreDream-6Bold, sans-serif;
  font-size: 3.2vw;
  margin: 0;
  color: #ffdcdc;
}
h2 {
  font-size: 2.857vw;
}
h4 {
  font-size: 2.4vw;
}
.movingDots img {
  position: absolute;
  /* border:1px solid blueviolet; */
}
.pink {
  background: #ffaaa6;
  border-radius: 3%;
  color: #ffffff;
  text-shadow: none;
  padding: 0 1.1rem;
}

#card{
  position: absolute;

  /* border:3px solid red; */
  transform: translate(25%,-25%) scale(0.4);
  z-index: -1;
}


.movingDots > img:nth-child(1) {
  z-index: -10;
  animation: orangeDot 1s ease-in Infinite Alternate;
}
.movingDots > img:nth-child(2) {
  animation: peachDot 1.5s ease-in Infinite Alternate;
}
.movingDots > img:nth-child(3) {
  animation: peachDot 1s ease-in Infinite Alternate;
}

@keyframes orangeDot {
  0% {
    margin-top: 0px;
  }
  to {
    margin-top: 10px;
  }
}
@keyframes peachDot {
  0% {
    margin-top: 10px;
  }
  to {
    margin-top: 0px;
  }
}
@keyframes lines {
  0% {
    margin-top: 0px;
  }
  to {
    margin-top: 10px;
  }
}




@media (max-width: 1023px) {
  html, body{
    font-size: 7px;
  }
  #card{
    position: absolute;
    transform: translate(-4%,-17%) scale(0.6);
    z-index: 1;
    
  }
  .greeting{
    position: static;
    width: 100%;
    transform: translateY(14%);
  }
}
 
@media (max-width: 767px) {
  html, body{
    font-size: 7px;
  }
  #card{
    position: absolute;
    transform: translate(-4%,-17%) scale(0.6);
    z-index: 1;
    
  }
  .greeting{
    position: static;
    width: 100%;
    transform: translateY(14%);
  }
}
 
@media (max-width: 639px) {
  html, body{
    font-size: 5px;
  }
  #card{
    position: absolute;
    transform: translate(-4%,16%) scale(0.9);
    z-index: 1;
    
  }
  .greeting{
    position: static;
    width: 100%;
    transform: translateY(36%);
  }
  .movingDots{
    z-index: -10;
  }
}