.nav-container {
  /* border:1px solid blue; */
  font-family: ELAND_Choice_M, sans-serif;
  font-size: 3.8rem;
  text-shadow: 1.5px 0px #39393d, 0px 1.5px #39393d, -1.5px 0px #39393d,
    0px -1.5px #39393d;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  color: #ffffff;
  background-color: #ffffff;

  display: flex;
  padding: 0rem 15rem 0rem 15rem;
  height: 10%;
  align-items: center;

  position: fixed;
  top:0;
  left:0;
  right:0;
  z-index: 999;
  
}
.left-nav {
  display: flex;
  list-style: none;
  justify-content: flex-start;
  min-width: 40%;
  align-items: center;
  flex-wrap: wrap;

}

.right-nav {
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: flex-end;
  min-width: 60%;
  flex-wrap: wrap;
  flex-wrap: nowrap;
}
.nav-item.padding {
  padding: 0 4rem 0 0;
  white-space: nowrap;
}
.nav-item.square {
  border: 1.5px solid #39393d;
  border-radius: 15% 10%;
  background-color: #ffffff;
  height: 0.2rem;
  width: 8rem;
  margin: 0 0 0 2rem;
}
.nav-item a{
  text-decoration: none;
  color:#ffffff;
}
@media (max-width: 639px) {
  .nav-container{
    height: 5%;
    padding: 0 1.5rem;
  }
  .left-nav{
    width: 10%;
  }
}